%h1 {
  font-family: "Sharp Sans Display No2", sans-serif;
  font-size: 60px;
  font-weight: 600;
  line-height: 1.2;
  color: $mainGray;
}

%h2 {
  font-family: "Sharp Sans Display No2", sans-serif;
  font-size: 36px;
  font-weight: 600;
  line-height: 1.5;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: $mainGreen;
}

%h3 {
  font-family: "Sharp Sans Display No2", sans-serif;
  font-size: 20px;
  font-weight: 600;
  line-height: 30px;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: $mainGray;
}

%p {
  font-family: "Sharp Sans Display No2", sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.5;
  color: $mainGray;
}

%button {
  width: 280px;
  height: 60px;
  font-size: 16px;
  line-height: 60px;
  text-transform: uppercase;
  color: $buttonNameGray;
  background-color: $mainGray;
  cursor: pointer;
  transition: 0.3s;

  &:hover {
    color: $mainGray;
    background-color: transparent;
    border: 1px solid $mainGray;
  }
}
